@import url(https://fonts.googleapis.com/css2?family=Karla:wght@600&family=Maitree&family=Overlock&display=swap);

html {
  box-sizing: border-box;
  background-color: #cccccc;
}

h1 {
  font-family: "Karla", sans-serif;
  letter-spacing: -1px;
}

h2 {
  font-family: "Overlock", cursive;
}

.example-text {
  flex: 4 0;
  font-family: "Maitree", serif;
  font-size: large;
}

button:active,
button:focus {
  border-style: none;
  outline: none;
}

.notable-speeds {
  flex: 1 0;
}

li {
  font-family: "Overlock", cursive;
}

